.gpt3__blog {
  @apply flex flex-col;
}

.gpt3_blog-heading {
  @apply w-full text-left;
  margin-bottom: 5rem;
}

.gpt3_blog-heading h1 {
  @apply font-extrabold text-6xl leading-tight;
  letter-spacing: -0.04rem;
  font-family: var(--font-family);
  max-width: 700px;
}

.gpt3_blog-container {
  display: grid;
  grid-template-areas:
    "one one two three"
    "one one four five";
  gap: 2rem;
}

.g1 {
  grid-area: one;
}

.g2 {
  grid-area: two;
}
.g3 {
  grid-area: three;
}
.g4 {
  grid-area: four;
}
.g5 {
  grid-area: five;
}

@media screen and (max-width: 1090px) {
  .gpt3_blog-container {
    grid-template-areas:
      "two three"
      "four five"
      "one one";
    gap: 1.5rem;
  }
}

@media screen and (max-width: 750px) {
  .gpt3_blog-container {
    grid-template-areas:
      "two"
      "three"
      "four"
      "five"
      "one";
    gap: 1.5rem;
  }
}
