@tailwind base;
@tailwind components;
@tailwind utilities;

.gpt3__whatgpt3 {
  background: var(--color-footer);
  @apply flex flex-col p-8 bg-gradient-to-br from-blue-900 via-sky-900 to-sky-700;
}

.gpt3__whatgpt3-feature {
}

.gpt3__whatgpt3-feature .gpt3__features-container__feature {
  margin: 0;
}

.gpt3__whatgpt3-feature .gpt3__features-container__feature-text {
  max-width: 700px;
}

.gpt3__whatgpt3-heading {
  @apply flex flex-row justify-between items-center mt-16 mr-0 mb-8;
}

.gpt3__whatgpt3-heading h1 {
  @apply text-4xl leading-10 font-extrabold max-w-lg;
  font-family: var(--font-family);
}

.gpt3__whatgpt3-heading p {
  @apply text-base leading-8 font-medium cursor-pointer;
  font-family: var(--font-family);
  color: var(--color-subtext);
}

.gpt3__whatgpt3-container {
  @apply flex flex-wrap flex-row mt-8;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature {
  @apply flex-1 m-4 min-w-52 flex-col;
  display: unset;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature-text {
  margin: 0.5rem;
}

@media screen and (max-width: 850px) {
  .gpt3__whatgpt3-heading {
    @apply flex-col items-start mb-0;
    /* flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem; */
  }

  .gpt3__whatgpt3-heading p {
    margin-top: 1rem;
  }
}

@media screen and (max-width: 650px) {
  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-feature .gpt3__features-container__feature {
    flex-direction: column;
  }

  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
  }

  .gpt3__whatgpt3-heading h1 {
    font-size: 28px;
    line-height: 40px;
  }
}

@media screen and (max-width: 350px) {
  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-container .gpt3__features-container__feature {
    margin: 1rem 0;
    min-width: 100%;
  }
}
