.gpt3__footer {
  @apply flex flex-col text-white justify-center items-center w-full;
  background: var(--color-footer);
}

.gpt3__footer-heading {
  @apply w-full text-center mb-12;
}

.gpt3__footer-heading h1 {
  font-family: var(--font-family);
  @apply font-extrabold text-6xl text-center px-44;
  letter-spacing: -0.04rem;
}

.gpt3__footer-btn {
  border: 1px solid white;
  @apply flex justify-center items-center p-4
    text-center mb-40 cursor-pointer;
}

.gpt3__footer-btn p {
  box-sizing: border-box;
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 21px;
  color: white;
  word-spacing: 2px;
}

.gpt3__footer-link {
  @apply flex justify-between items-start flex-wrap flex-row w-full text-left;
}

.gpt3__footer-link_logo {
  @apply flex flex-col;
}

.gpt3__footer-link_logo img {
  @apply w-28 h-8 mb-8;
}

.gpt3__footer-link_logo p {
  font-family: var(--font-family);
  @apply text-xs text-white;
}

.gpt3__footer-link_div {
  width: 250px;
  margin: 1rem;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.gpt3__footer-link_div h4 {
  @apply text-sm font-bold text-white mb-4;
  font-family: var(--font-family);
}

.gpt3__footer-link_div p {
  font-family: var(--font-family);
  @apply text-xs text-white my-2 cursor-pointer;
}

.gpt3__footer-copyright {
  @apply mt-8 text-center w-full;
}

.gpt3__footer-copyright p {
  @apply text-sm text-white;
  font-family: var(--font-family);
}

@media screen and (max-width: 850px) {
  .gpt3__footer-heading h1 {
    padding: 0;
    font-size: 3rem;
    line-height: 50px;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__footer-heading h1 {
    font-size: 27px;
    line-height: 38px;
  }

  .gpt3__footer-link div {
    margin: 1rem 0;
  }

  .gpt3__footer-btn p {
    font-size: 14px;
    line-height: 20px;
  }
}
