@tailwind base;
@tailwind components;
@tailwind utilities;

.gpt3__header {
  @apply flex;
}

.gpt3__header-content {
  @apply flex-1 flex justify-center items-start flex-col mr-20;
}

.gpt3__header-content h1 {
  @apply font-extrabold text-6xl leading-tight;
  font-family: var(--font-family);
  letter-spacing: -0.04em;
}

.gpt3__header-content p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: normal;
  @apply text-xl leading-7 mt-6;
  color: var(--color-text);
}

.gpt3__header-content__input {
  @apply w-full mt-8 mr-0 mb-4 flex flex-row;
}

.gpt3__header-content__input input {
  @apply bg-cyan-950 w-full min-h-12 text-xl leading-7 px-4 text-white;
  flex: 2;
  border-radius: 5px 0 0 5px;
  font-family: var(--font-family);
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
}

.gpt3__header-content__input button {
  @apply bg-orange-600 text-xl leading-7 text-white w-full min-h-12 font-bold cursor-pointer outline-none;
  flex: 0.6;
  font-family: var(--font-family);
  border-radius: 0 5px 5px 0;
}

.gpt3__header-content__people {
  @apply w-full flex justify-start items-center mt-8;
}

.gpt3__header-content__people img {
  @apply w-44 h-9;
}

.gpt3__header-content__people p {
  @apply ml-4 mr-0 my-0 font-medium text-xs leading-9 text-white text-center;
  font-family: var(--font-family);
}

.gpt3__header-image {
  @apply flex-1 flex justify-center items-center;
}

.gpt3__header-image img {
  @apply w-full h-full;
}

@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header h1 {
    font-size: 48px;
    line-height: 1.25;
  }

  .gpt3__header p {
    @apply text-base leading-6;
  }

  .gpt3__header-content__people {
    @apply flex-col;
  }

  .gpt3__header-content__people p {
    margin: 0;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header h1 {
    font-size: 36px;
    line-height: 1.2;
  }

  .gpt3__header p {
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}
