.gpt3__cta {
  @apply border-solid border border-white text-white flex justify-between items-center flex-row
    p-8 m-16 rounded-3xl;
  background: var(--gradient-bar);
}

.gpt3__cta-content {
}

.gpt3__cta-content p {
  font-family: var(--font-family);
  @apply font-medium text-xl leading-tight;
  color: #0e0e0e;
}

.gpt3__cta-content h3 {
  font-family: var(--font-family);
  @apply font-extrabold text-2xl leading-tight;
  color: #000000;
}

.gpt3__cta-btn {
  @apply flex justify-center items-center ml-8;
}

.gpt3__cta-btn button {
  background: #000000;
  @apply text-lg font-extrabold leading-8 my-2 mx-4 min-w-36 px-2 py-4;
  border-radius: 40px;
  color: white;
  font-family: var(--font-family);
}

@media screen and (max-width: 650px) {
  .gpt3__cta {
    flex-direction: column;
  }

  .gpt3__cta-btn {
    margin: 2rem 0 0;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__cta {
    margin: 4rem 2rem;
  }

  .gpt3__cta-content h3 {
    font-size: 18px;
    line-height: 32px;
  }

  .gpt3__cta-btn button {
    font-size: 16px;
    line-height: 30px;
  }
}
