@tailwind base;
@tailwind components;
@tailwind utilities;

.gpt3__navbar-sign {
  @apply flex justify-end items-center;
}

.gpt3__navbar-link_container {
  @apply flex flex-row;
}

.gpt3__navbar-link_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
  @apply text-white font-medium text-base capitalize my-0 mx-8 cursor-pointer;
  font-family: var(--font-family);
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
  @apply py-2 px-4 text-white bg-orange-600 font-medium text-base rounded-md;
  font-family: var(--font-family);
}

.gpt3__navbar-menu {
  @apply ml-4 hidden relative;
}

.gpt3__navbar-menu svg {
  @apply cursor-pointer;
}

.gpt3__navbar-menu_container {
  @apply flex justify-end items-end flex-col text-end p-8 absolute top-10 right-0 mt-4 min-w-52 rounded-md;
  background: var(--color-footer);
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-link_container p {
  @apply my-4 mx-6;
}

.gpt3__navbar-menu_container-link-sign {
  @apply hidden;
}

@media screen and (max-width: 1050px) {
  .gpt3__navbar-link_container {
    display: none;
  }

  .gpt3__navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }

  .gpt3__navbar-sign {
    display: none;
  }

  .gpt3__navbar-menu_container {
    top: 20px;
  }

  .gpt3__navbar-menu_container-link-sign {
    display: block;
  }
}
