.gpt3__posibility {
  @apply flex flex-row justify-center items-center;
}

.gpt3__posibility-image {
  @apply flex-1 flex justify-start items-center mr-8;
}

.gpt3__posibility-image img {
  @apply w-full h-full;
}

.gpt3__posibility-content {
  @apply flex-1 flex flex-col justify-end items-start;
}

.gpt3__posibility-content h4 {
  font-family: var(--font-family);
  @apply font-medium text-2xl;
  color: #71e5ff;
}

.gpt3__posibility-content h4:last-child {
  color: #ff8a71;
}

.gpt3__posibility-content h1 {
  font-family: var(--font-family);
  @apply font-extrabold text-4xl leading-tight my-4;
  color: #71e5ff;
}

.gpt3__posibility-content p {
  font-family: var(--font-family);
  @apply font-medium text-xl mb-8;
  color: var(--color-text);
}

@media screen and (max-width: 1050px) {
  .gpt3__posibility-content h4 {
    @apply font-medium text-xl;
  }

  .gpt3__posibility-content h1 {
    @apply font-extrabold text-3xl leading-tight my-4;
  }

  .gpt3__posibility-content p {
    @apply font-medium text-lg mb-8;
  }
}

@media screen and (max-width: 950px) {
  .gpt3__posibility {
    flex-direction: column;
  }

  .gpt3__posibility-image {
    margin: 1rem 0;
  }

  .gpt3__posibility-content {
    margin-top: 2rem;
  }
}
